













































































































export default {
	data() {
		return {
			personal_datas:[]
		};
	},
	onLoad:function(){
		var token=(uni.getStorageSync("user_info")).token;
		console.info(token);
		uni.request({
			url: 'http://bus.liebianzhe.com/api/user/get_user_data',
			method: 'POST',
			data: {
				token:token
			},
			success: res => {
				console.info(res);
				if(res.data.code==1){
					this.personal_datas=res.data.data;
					console.info(this.personal_datas);
				}else if(res.data.code==10){
					uni.removeStorage({
						key:'user_info',
					})
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
					setTimeout(function(){
						uni.reLaunch({
							url:'../login/login'
						})
					},1500)
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1500
					});
				}
			},
			fail: () => {
				console.info("请求失败~");
			},
		});
	},
	methods:{
		upload(){
			uni.navigateTo({
				url:'../personal-center/upload'
			})
		},
